body{font-family:Lato;font-weight:300;color:#111}.noBorder{border:0 solid #fff!important;borderTop:0 solid #fff!important}.clickable{-webkit-user-select:none;-moz-user-select:none;-khtml-user-select:none;-ms-user-select:none;cursor:pointer;outline:none}.ui.icon.input{padding:3px 5px;width:100%;max-width:350px}.ui.search .prompt{border:1px solid #85b7d9;border-radius:.35rem!important}.ui.search .ui.input input{padding:.3em 1em}.ui.search .result .description,.ui.search .result .title{display:inline-block;padding-right:10px}.ui.search .results{margin-top:0;margin-left:5px}.ui.search .results .content{white-space:nowrap}.ui.search .results .message .header{color:#333;font-weight:400}.ui.search>.results .result .title{font-weight:500;font-size:.95em}.ui.selection.dropdown{padding:.3em 2.1em .3em .5em;min-height:0!important}.ui.selection.dropdown .menu>.item{padding:.7em .5em!important}.ui.selection.dropdown>.dropdown.icon{top:.4em!important;right:.5em!important}


